/**
 * 
 * @param {Object} subtask - 
 * @returns {string} : 'completed' | 'in_progress' | 'overdue'
 */
export const calculateSubtaskStatus = (subtask) => {
  // 
  if (subtask.status === 'completed') {
    return 'completed';
  }
  
  const now = new Date();
  
  // 
  if (subtask.endTime) {
    const endTime = new Date(subtask.endTime);
    if (now > endTime) {
      return 'overdue';
    }
  }
  
  // 
  return 'in_progress';
};

/**
 * 
 * @param {Object} milestone - 
 * @param {Array} subtasks - 
 * @returns {string} : 'completed' | 'in_progress' | 'overdue'
 */
export const calculateMilestoneStatus = (milestone, subtasks) => {
  const now = new Date();
  
  // 
  if (milestone.endTime) {
    const milestoneEnd = new Date(milestone.endTime);
    if (now > milestoneEnd) {
      // 
      if (!subtasks || subtasks.length === 0) {
        // 
        return 'overdue';
      } else {
        // 
        const completedSubtasks = subtasks.filter(s => s.status === 'completed');
        if (completedSubtasks.length === subtasks.length) {
          // 
          return 'completed';
        } else {
          // 
          return 'overdue';
        }
      }
    }
  }
  
  // 
  if (!subtasks || subtasks.length === 0) {
    return 'in_progress';
  }
  
  const completedSubtasks = subtasks.filter(s => s.status === 'completed');
  
  // 
  if (completedSubtasks.length === subtasks.length) {
    return 'completed';
  }
  
  return 'in_progress';
};

/**
 * 
 * @param {Object} project - 
 * @param {Array} milestones - 
 * @param {Object} milestoneSubtasks - ID
 * @returns {string} : '' | '' | ''
 */
export const calculateProjectStatus = (project, milestones, milestoneSubtasks = {}) => {
  const now = new Date();
  
  // 
  if (project.endTime) {
    const endTime = new Date(project.endTime);
    if (now > endTime) {
      // 
      if (!milestones || milestones.length === 0) {
        // 
        return '';
      }
      
      // 
      let allMilestonesCompleted = true;
      
      for (const milestone of milestones) {
        // 
        const subtasks = milestoneSubtasks[milestone.milestoneId] || [];
        const actualMilestoneStatus = calculateMilestoneStatus(milestone, subtasks);
        
        if (actualMilestoneStatus !== 'completed') {
          allMilestonesCompleted = false;
          break;
        }
      }
      
      // 
      if (!allMilestonesCompleted) {
        return '';
      } else {
        // 
        return '';
      }
    }
  }
  
  // 
  if (!milestones || milestones.length === 0) {
    return '';
  }
  

  
  // 
  let allMilestonesCompleted = true;
  
  for (const milestone of milestones) {
    // 
    const subtasks = milestoneSubtasks[milestone.milestoneId] || [];
    const actualMilestoneStatus = calculateMilestoneStatus(milestone, subtasks);
    
    if (actualMilestoneStatus !== 'completed') {
      allMilestonesCompleted = false;
      break;
    }
  }
  
  // 
  if (allMilestonesCompleted) {
    return '';
  }
  
  return '';
};

/**
 * API
 * @param {string} chineseStatus - 
 * @returns {string} 
 */
export const convertStatusToEnglish = (chineseStatus) => {
  switch (chineseStatus) {
    case '':
      return 'completed'
    case '':
      return 'overdue'
    case '':
      return 'in_progress'
    default:
      return 'in_progress'
  }
}

/**
 * 
 * @param {string} status - 
 * @returns {string} 
 */
export const getStatusColor = (status) => {
  const colors = {
    'in_progress': '#3b82f6',
    '': '#3b82f6',
    'completed': '#10b981',
    '': '#10b981',
    'overdue': '#ef4444',
    '': '#ef4444'
  };
  return colors[status] || colors['in_progress'];
};

/**
 * 
 * @param {string} status - 
 * @returns {string} 
 */
export const getStatusText = (status) => {
  const texts = {
    'in_progress': '',
    '': '',
    'completed': '',
    '': '',
    'overdue': '',
    '': ''
  };
  return texts[status] || '';
};
