import React, { useState, useEffect } from 'react';
import api, { projectAPI, subtaskAPI, githubAPI } from '../../utils/api';
import { accountAPI } from '../../utils/api';
import './Profile.css';

// 
const validatePasswordStrength = (password) => {
  const feedback = [];
  let score = 0;

  // 
  if (password.length < 8) {
    feedback.push('8');
  } else if (password.length >= 12) {
    score += 2;
  } else {
    score += 1;
  }

  // 
  if (/\d/.test(password)) {
    score += 1;
  } else {
    feedback.push('');
  }

  // 
  if (/[a-z]/.test(password)) {
    score += 1;
  } else {
    feedback.push('');
  }

  // 
  if (/[A-Z]/.test(password)) {
    score += 1;
  } else {
    feedback.push('');
  }

  // 
  if (/[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]/.test(password)) {
    score += 1;
  } else {
    feedback.push('');
  }

  // 
  const weakPasswords = ['123456', 'password', 'qwerty', 'admin', '123456789'];
  if (weakPasswords.includes(password.toLowerCase())) {
    feedback.push('');
    score = 0;
  }

  return { score, feedback };
};

const Profile = ({ user }) => {
  const [userInfo, setUserInfo] = useState(user);
  const [ghBinding, setGhBinding] = useState({ bound: false });
  const [editing, setEditing] = useState(false);
  const [showPrivateKey, setShowPrivateKey] = useState(false);
  const [privateKeyData, setPrivateKeyData] = useState(null);
  const [passwordForKey, setPasswordForKey] = useState('');
  const [loadingPrivateKey, setLoadingPrivateKey] = useState(false);
  const [editForm, setEditForm] = useState({
    username: user?.username || '',
    newPassword: '',
    confirmPassword: ''
  });


  const handleGetPrivateKey = async () => {
    if (!passwordForKey.trim()) {
      alert('');
      return;
    }

    setLoadingPrivateKey(true);
    try {
      const response = await api.post('/api/accounts/getPrivateKey', {
        username: user.username,
        password: passwordForKey
      });

      if (response.data.success) {
        setPrivateKeyData(response.data.data);
        setShowPrivateKey(true);
        setPasswordForKey('');
      } else {
        alert(response.data.message || '');
      }
    } catch (error) {
      console.error(':', error);
      alert(error.response?.data?.message || '');
    } finally {
      setLoadingPrivateKey(false);
    }
  };

  const handleUpdateProfile = async (e) => {
    e.preventDefault();
    
    if (editForm.newPassword && editForm.newPassword !== editForm.confirmPassword) {
      alert('');
      return;
    }

    if (editForm.newPassword && editForm.newPassword.length < 8) {
      alert('8');
      return;
    }

    // 
    if (editForm.newPassword) {
      const passwordValidation = validatePasswordStrength(editForm.newPassword);
      if (passwordValidation.score < 3) {
        alert(`: ${passwordValidation.feedback.join(', ')}`);
        return;
      }
    }

    try {
      const updateData = {
        username: editForm.username
      };
      
      if (editForm.newPassword) {
        updateData.password = editForm.newPassword;
      }

      const response = await accountAPI.updateProfile(updateData);
      if (response.ok) {
        setUserInfo({ ...userInfo, username: editForm.username });
        setEditing(false);
        setEditForm({ ...editForm, newPassword: '', confirmPassword: '' });
        alert('');
      } else {
        alert(response.error?.message || '');
      }
    } catch (error) {
      console.error(':', error);
      alert(error.response?.data?.message || '');
    }
  };

  const copyToClipboard = (text) => {
    navigator.clipboard.writeText(text).then(() => {
      alert('');
    });
  };

  useEffect(() => {
    const fetchBinding = async () => {
      const res = await accountAPI.getGithubBinding();
      if (res.ok) setGhBinding(res.data);
    };
    fetchBinding();
  }, []);

  const handleUnbind = async () => {
    const ok = window.confirm(' GitHub ');
    if (!ok) return;
    const res = await accountAPI.unbindGithub();
    if (res.ok) {
      alert(' GitHub');
      setGhBinding({ bound: false });
    } else {
      alert(res.error?.message || '');
    }
  };

  const handleConnectGithub = async () => {
    try {
      const resp = await githubAPI.getAuthUrl();
      const authUrl = resp?.data?.authUrl;
      if (authUrl) {
        window.location.href = authUrl;
      } else {
        alert('GitHub');
      }
    } catch (e) {
      console.error('GitHub:', e);
      alert('GitHub');
    }
  };

  return (
    <div className="profile">
      <div className="profile-header">
        <h1> </h1>
        <p></p>
      </div>

      <div className="profile-content">
        {/*  */}
        <div className="profile-card">
          <div className="profile-avatar-section">
            <div className="user-avatar-large">
              {userInfo.username.charAt(0).toUpperCase()}
            </div>
            <div className="user-info">
              <h2>{userInfo.username}</h2>
              <div className="user-address-container">
                <span className="address-label">:</span>
                <span className="user-address">{userInfo.address}</span>
              </div>
              <div className="github-binding">
                <span className="github-label">GitHub </span>
                {ghBinding.bound ? (
                  <div className="github-bound">
                    {ghBinding.github_avatar && (
                      <img className="github-avatar" src={ghBinding.github_avatar} alt={ghBinding.github_login} />
                    )}
                    <a className="github-login" href={`https://github.com/${ghBinding.github_login}`} target="_blank" rel="noreferrer">@{ghBinding.github_login}</a>
                    <button className="github-unbind" onClick={handleUnbind}></button>
                  </div>
                ) : (
                  <button type="button" className="github-bind-btn" onClick={handleConnectGithub}>  GitHub</button>
                )}
              </div>
            </div>
          </div>
          
          {!editing ? (
            <button className="edit-btn" onClick={() => setEditing(true)}>
              
            </button>
          ) : (
            <form onSubmit={handleUpdateProfile} className="edit-form">
              <div className="form-group">
                <label></label>
                <input
                  type="text"
                  value={editForm.username}
                  onChange={(e) => setEditForm({...editForm, username: e.target.value})}
                  required
                />
              </div>
              
              <div className="form-group">
                <label></label>
                <input
                  type="password"
                  value={editForm.newPassword}
                  onChange={(e) => setEditForm({...editForm, newPassword: e.target.value})}
                  placeholder=""
                />
              </div>
              
              <div className="form-group">
                <label></label>
                <input
                  type="password"
                  value={editForm.confirmPassword}
                  onChange={(e) => setEditForm({...editForm, confirmPassword: e.target.value})}
                  placeholder=""
                />
              </div>
              
              <div className="form-actions">
                <button type="submit" className="save-btn"></button>
                <button type="button" className="cancel-btn" onClick={() => setEditing(false)}>
                  
                </button>
              </div>
            </form>
          )}
        </div>



        {/*  */}
        <div className="private-key-section">
          <div className="section-header">
            <h3> </h3>
            <p></p>
          </div>
          <div className="warning-box">
            <div className="warning-icon"></div>
            <div className="warning-content">
              <h4></h4>
              <p></p>
            </div>
          </div>
          
          {!showPrivateKey ? (
            <div className="private-key-form">
              <div className="form-group">
                <label></label>
                <input
                  type="password"
                  value={passwordForKey}
                  onChange={(e) => setPasswordForKey(e.target.value)}
                  placeholder=""
                  onKeyPress={(e) => e.key === 'Enter' && handleGetPrivateKey()}
                />
              </div>
              <button 
                className="show-key-btn" 
                onClick={handleGetPrivateKey}
                disabled={loadingPrivateKey}
              >
                {loadingPrivateKey ? '...' : ''}
              </button>
            </div>
          ) : (
            <div className="private-key-display">
              <div className="key-info">
                <div className="key-item">
                  <label></label>
                  <div className="key-value">
                    <span>{privateKeyData?.address}</span>
                    <button onClick={() => copyToClipboard(privateKeyData?.address)}>
                       
                    </button>
                  </div>
                </div>
                
                <div className="key-item">
                  <label></label>
                  <div className="key-value private-key">
                    <span>{privateKeyData?.privateKey}</span>
                    <button onClick={() => copyToClipboard(privateKeyData?.privateKey)}>
                       
                    </button>
                  </div>
                </div>
              </div>
              
              <button 
                className="hide-key-btn" 
                onClick={() => {
                  setShowPrivateKey(false);
                  setPrivateKeyData(null);
                }}
              >
                
              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default Profile;

