import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import './Auth.css';
import { accountAPI } from '../../utils/api';

const Register = () => {
  const [formData, setFormData] = useState({
    username: '',
    // email: '',
    password: '',
    confirmPassword: '',
    // walletAddress: ''
  });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [passwordStrength, setPasswordStrength] = useState({
    score: 0,
    feedback: []
  });
  const navigate = useNavigate();

  // 
  const validatePasswordStrength = (password) => {
    const feedback = [];
    let score = 0;

    // 
    if (password.length < 8) {
      feedback.push('8');
    } else if (password.length >= 12) {
      score += 2;
    } else {
      score += 1;
    }

    // 
    if (/\d/.test(password)) {
      score += 1;
    } else {
      feedback.push('');
    }

    // 
    if (/[a-z]/.test(password)) {
      score += 1;
    } else {
      feedback.push('');
    }

    // 
    if (/[A-Z]/.test(password)) {
      score += 1;
    } else {
      feedback.push('');
    }

    // 
    if (/[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]/.test(password)) {
      score += 1;
    } else {
      feedback.push('');
    }

    // 
    const weakPasswords = ['123456', 'password', 'qwerty', 'admin', '123456789'];
    if (weakPasswords.includes(password.toLowerCase())) {
      feedback.push('');
      score = 0;
    }

    return { score, feedback };
  };

  const handlePasswordChange = (e) => {
    const password = e.target.value;
    setFormData({...formData, password});
    
    if (password) {
      const strength = validatePasswordStrength(password);
      setPasswordStrength(strength);
    } else {
      setPasswordStrength({ score: 0, feedback: [] });
    }
  };

  const getPasswordStrengthColor = (score) => {
    if (score >= 4) return '#28a745'; //  - 
    if (score >= 3) return '#ffc107'; //  - 
    if (score >= 2) return '#fd7e14'; //  - 
    return '#dc3545'; //  - 
  };

  const getPasswordStrengthText = (score) => {
    if (score >= 4) return '';
    if (score >= 3) return '';
    if (score >= 2) return '';
    return '';
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError('');

    if (formData.password !== formData.confirmPassword) {
      setError('');
      setLoading(false);
      return;
    }

    // 
    if (formData.password.length < 8) {
      setError('8');
      setLoading(false);
      return;
    }

    if (passwordStrength.score < 3) {
      setError('');
      setLoading(false);
      return;
    }

    try {
      // API
      const response = await accountAPI.register({
        username: formData.username,
        password: formData.password
      });
      
      if (response.data.success) {
        // 
        navigate('/login');
      } else {
        setError(response.data.message || '');
      }
    } catch (err) {
      console.error(':', err);
      if (err.response?.data?.message) {
        setError(err.response.data.message);
      } else {
        setError('');
      }
    } finally {
      setLoading(false);
    }
  };
  
  return (
    <div className="auth-container">
      {/*  */}
      <div className="particle particle-1"></div>
      <div className="particle particle-2"></div>
      <div className="particle particle-3"></div>
      <div className="particle particle-4"></div>
      <div className="particle particle-5"></div>
      
      {/*  */}
      <div className="border-glow">
        <div className="glow-point glow-point-1"></div>
        <div className="glow-point glow-point-2"></div>
        <div className="glow-point glow-point-3"></div>
        <div className="glow-point glow-point-4"></div>
      </div>
      
      <div className="auth-left">
        <div className="auth-illustration">
          <div className="blockchain-visual">
            <div className="block"></div>
            <div className="block"></div>
            <div className="block"></div>
          </div>
          <h2></h2>
          <p></p>
        </div>
      </div>
      <div className="auth-right">
        <div className="auth-form-container">
          <div className="auth-header">
            <h1></h1>
            <p></p>
          </div>
          
          <form onSubmit={handleSubmit} className="auth-form">
            {error && <div className="error-message">{error}</div>}
            
            <div className="form-group">
              <label></label>
              <input
                type="text"
                value={formData.username}
                onChange={(e) => setFormData({...formData, username: e.target.value})}
                placeholder=""
                required
              />
            </div>
            
            
            
            <div className="form-group">
              <label></label>
              <input
                type="password"
                value={formData.password}
                onChange={handlePasswordChange}
                placeholder="8"
                required
              />
              {formData.password && (
                <div className="password-strength-container">
                  <div className="password-strength-bar">
                    <div 
                      className="password-strength-fill" 
                      style={{ 
                        width: `${(passwordStrength.score / 5) * 100}%`,
                        backgroundColor: getPasswordStrengthColor(passwordStrength.score)
                      }}
                    ></div>
                  </div>
                  <span className="password-strength-text" style={{ color: getPasswordStrengthColor(passwordStrength.score) }}>
                    : {getPasswordStrengthText(passwordStrength.score)}
                  </span>
                  {passwordStrength.feedback.length > 0 && (
                    <ul className="password-strength-feedback">
                      {passwordStrength.feedback.map((item, index) => (
                        <li key={index} className="feedback-item"> {item}</li>
                      ))}
                    </ul>
                  )}
                </div>
              )}
            </div>
            
            <div className="form-group">
              <label></label>
              <input
                type="password"
                value={formData.confirmPassword}
                onChange={(e) => setFormData({...formData, confirmPassword: e.target.value})}
                placeholder=""
                required
              />
            </div>
            
            
            
            <button type="submit" className="auth-btn" disabled={loading}>
              {loading ? '...' : ''}
            </button>
          </form>
          
          <div className="auth-footer">
            <p> <Link to="/login"></Link></p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Register;